.game-wrapper {
  display: flex;
  flex-flow: column wrap;
  gap: 10px;
  align-items: center;
  justify-content: center;
}

.game-items {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(9, 1fr);
  gap: 15px;
  justify-items: center;
}

.game-top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  width: 975px;
  height: 91px;
}

.game-timer {
  display: flex;
  flex-flow: column;
  width: 160px;
  color: $main-text-color;

  &-minutes,
  &-seconds {
    position: relative;
  }

  &-minutes::after,
  &-seconds::after {
    position: absolute;
    top: -20px;
    right: 0;
    font-size: 16px;
    line-height: 32px;
    content: "min";
  }

  &-seconds::after {
    content: "sec";
  }
}

.timer-wrapper {
  display: grid;
  grid-template-columns: 1fr 20px 1fr;
  font-size: 64px;
  line-height: 72px;
  text-align: center;
}
